<!-- 装桶日统计报表 -->
<template>
  <div>
    <Row>
      <Date-picker type="date" format="yyyy/MM/dd" placeholder="选择日期"></Date-picker>
      <Button type="success" class="margin-left">
        <Icon type="md-search" />确定
      </Button>
    </Row>
    <br />
    <Table :columns="column1" :data="data1" border></Table>
  </div>
</template>

<script>
export default {
  name: "BarrellDay",
  data() {
    return {
      column1: [
        {
          key: "date",
          title: "时间",
          align: "center"
        },
        {
          key: "drugInducedKg",
          title: "重量",
          align: "center"
        },
        {
          key: "drugInducedNum",
          title: "数量",
          align: "center"
        }
      ]
    };
  }
};
</script>

